.odc-monitoring-dashboard {
  position: relative;
}

.odc-monitoring-dashboard__dropdown-options {
  align-self: flex-end;
  display: flex;
  position: absolute;
  right: 30px;
  top: -65px;
}

@media (max-width: 915px) {
  .odc-monitoring-dashboard__dropdown-options {
    display: block;
    margin-left: 15px;
    margin-top: 15px;
    position: initial;
  }
}

@media (max-width: 915px) and (min-width: 768px) {
  .odc-monitoring-dashboard__dropdown-options {
    margin-left: 30px;
  }
}

@media (max-width: 915px) {
  .odc-monitoring-dashboard__dropdown-options .monitoring-dashboards__dropdown-wrap {
    display: inline-block;
  }

  .odc-monitoring-dashboard__dropdown-options .monitoring-dashboards__dropdown-wrap .monitoring-dashboards__dropdown-title {
    margin-right: 10px;
  }
}

.odc-monitoring-dashboard__dropdown-options .monitoring-dashboards__dropdown-wrap:last-of-type {
  margin-right: 0;
}

.odc-monitoring-dashboard__resource-link {
  padding: var(--pf-global--spacer--sm) var(--pf-global--spacer--xl);
}

